import up from "../../assets/images/background/up.png";
import down from "../../assets/images/background/down.png";

import { ReactComponent as LogoIcon } from "../../assets/images/icon/colorLogo.svg";

import { Outlet, Link } from "react-router-dom";

import styles from "./publicRoute.module.scss";

const PublicRoute = () => {
  return (
    <div className={styles.container}>
      <img src={up} alt="" />
      <div className={styles.innerContainer}>
        <Link to={"/"}>
          <LogoIcon className={styles.logo} />
        </Link>
        <Outlet />
      </div>
      <img src={down} alt="" />
    </div>
  );
};

export default PublicRoute;
