import { BrowserRouter } from "react-router-dom";
import { Provider } from "react-redux";

import UserRouters from "./UserRoutes";

import store from "./redux/store";

const App = () => {
  return (
    <Provider store={store}>
      <BrowserRouter basename="/bubu-test-project">
        <UserRouters />
      </BrowserRouter>
    </Provider>
  );
};

export default App;
